.accordion-title {
  overflow: hidden;
  display: block;
  position: relative;
}
@media (max-width: 767.9px) {
  .accordion-title {
    margin: 0;
    padding: 21px 0;
    width: 100%;
    min-height: 60px;
    cursor: pointer;
  }
  .accordion-title::before {
    position: absolute;
    content: "";
    right: 0;
    top: 50%;
    width: 15px;
    height: 2px;
    background-color: var(--color-alice-blue3);
    transform: translateY(-50%);
  }
  .accordion-title::after {
    position: absolute;
    content: "";
    right: 6px;
    top: 50%;
    width: 2px;
    height: 15px;
    background-color: var(--color-alice-blue3);
    transform: translateY(-50%);
    transition: 0.3 opacity;
  }
}
.accordion-title.active::after {
  opacity: 0;
}
.accordion-title--none::before, .accordion-title--none::after {
  display: none;
}

@media (max-width: 767.9px) {
  .accordion-wrap {
    overflow: hidden;
    max-height: 0;
    transition: 0.3s max-height ease-out;
  }
  .accordion-wrap.no-js {
    max-height: 100%;
  }
}

